@font-face {
  font-family: 'nasalizationregular';
  src: url("./fonts/nasalization-rg-webfont.woff2") format("woff2"), url("./fonts/nasalization-rg-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

body {
  margin: 0;
  background: #e9ecef;
  box-sizing: border-box;
  font-family: 'nasalizationregular';
  width: 100%;
  height: 100%; }

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

#root > [data-reactroot] {
  height: 100%; }

html, body {
  width: 100vw;
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
  /* has to be scroll, not auto */
  -webkit-overflow-scrolling: touch;
  margin: 0;
  padding: 0; }

* {
  box-sizing: inherit; }



