.cards-wrap {
  margin: 2rem; }

.cards {
  width: 100%;
  max-width: 100%;
  margin: 20px auto 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-gap: 20px; }

.no-results {
  margin: 2rem auto;
  text-align: center; }

@media only screen and (min-width: 500px) {
  .cards {
    grid-template-columns: 1fr 1fr; } }

@media only screen and (min-width: 850px) {
  .cards {
    grid-template-columns: 1fr 1fr 1fr 1fr; } }



